.about {
  .content-box {
    .item {
      &:first-child {
        display: none;
      }
    }

    @include for-size(tablet) {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      margin-right: -15px;

      .item {
        &:first-child {
          display: flex;
          justify-content: center;
          margin-right: 15px;
        }
        flex-basis: calc((100% - 15px) / 2);
      }
    }
    @include for-size(desktop) {
      align-items: flex-start;

      .item:last-child {
        height: 530px;
        background-image: linear-gradient(
            90deg,
            rgba(255, 255, 255, 0.7) 0%,
            rgba(255, 255, 255, 0.7) 50%,
            rgba(255, 255, 255, 0.7) 100%
          ),
          url(../images/about/desktop/filter.png),
          url(../images/about/desktop/catrige.png),
          url(../images/about/desktop/system.png);
        background-repeat: no-repeat;
        background-size: contain, contain, 60%, 50%;
        background-position: 0% 0%, 10% 0%, 23% 105%, 100% 100%;
      }
    }

    .picture {
      position: relative;
      display: inline-block;

      &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        top: 15px;
        right: 15px;
        display: block;
        background-color: var(--btn-cl);
        z-index: -1;
        box-shadow: 3px 8px 24px 0px rgba(66, 68, 90, 1);
      }
    }
  }

  .text {
    text-align: justify;
    .name-company {
      font-weight: 700;
      text-transform: uppercase;
    }
    .accent {
      font-style: normal;
    }
    @include for-size(desktop) {
      font-size: 16px;
      font-weight: 500;
    }
  }
}
